async function currentData() {
  const currentData = (await Promise.resolve(weatherAPICall())).current;
  console.log(currentData);
  console.log(currentData.feelslike_c);
  console.log(currentData.humidity);
}

async function leftSideData() {
  const currentData = (await Promise.resolve(weatherAPICall())).current;
  const leftSideArray = []
  leftSideArray.push(currentData.humidity, currentData.pressure_in, currentData.uv, currentData.precip_mm, currentData.wind_kph, currentData.wind_dir);
  return leftSideArray;
}

async function middleData() {
  const currentData = (await Promise.resolve(weatherAPICall())).current;
  const middleArray = []
  middleArray.push(currentData.temp_c, currentData.condition.text);
  return middleArray;
}

async function rightSideData() {
  const currentData = (await Promise.resolve(weatherAPICall())).current.air_quality;
  const rightSideArray = []
  rightSideArray.push(currentData.co, currentData.no2, currentData.so2, currentData.pm2_5, currentData.pm10, currentData["us-epa-index"]);
  return rightSideArray;
}

async function restOfWeekData() {
  const currentData = (await Promise.resolve(weatherAPICall())).current;
  const restOfWeekArray = []
  restOfWeekArray.push();
  return restOfWeekArray;
}

async function locationData() {
  const locationData = (await Promise.resolve(weatherAPICall())).location;
  console.log(locationData);
  console.log(locationData.country);
  console.log(locationData.name);
}

